import Vue from 'vue'
import App from './App.vue'
import vuescroll from 'vuescroll';
import 'vuescroll/dist/vuescroll.css';
import { Loading, Cascader, Menu, Submenu, MenuItem, MenuItemGroup, Select, Option } from 'element-ui';
import '@/assets/flexible.js'

Vue.use(vuescroll);
Vue.use(Cascader);
Vue.use(Menu);
Vue.use(Submenu);
Vue.use(MenuItem);
Vue.use(Select);
Vue.use(Option);
Vue.use(MenuItemGroup);
Vue.use(Loading);
Vue.prototype.$vuescrollConfig = {
    bar: {
        keepShow: true,
        background: "#DDE6EE"
    },
    scrollPanel: {
        scrollingX: false
      }
};

Vue.config.productionTip = false

new Vue({
    render: h => h(App),
}).$mount('#app')