<template>
    <div id="app">
        <div class="window_con">
            <div class="list_wrap" v-show="!currLesson">
                <div class="title">
                    选择备课包
                </div>
                <div class="btn_close" @click="closeWindow">
                    <img src="@/assets/icon_close@2x.png" />
                </div>
                <div class="teachContent">
                    <div class="teachTree">
                        <div class="bookName">
                            <el-select v-model="bookId" placeholder="请选择" size="small" v-if="bookList"
                                @change="changeBook">
                                <el-option v-for="book in bookList" :key="book.bookId" :label="book.bookName"
                                    :value="book.bookId">
                                </el-option>
                            </el-select>
                        </div>
                        <div class="chapterTree" v-loading="baseLoading">
                            <vue-scroll ref="vsMenu">
                                <el-menu text-color="#2E4A66" :default-active="activeIndex" active-text-color="#2E4A66"
                                    :unique-opened="true" class="el-menu-vertical-demo" @select="selectLesson"
                                    @open="openSubmenu" v-if="chapterList" ref="tMenu">
                                    <template v-for="chapter in chapterList">
                                        <el-menu-item :index="chapter.id + ''" :disabled="chapter.disabled"
                                            v-if="chapter.metaLeaf">{{chapter.name?chapter.name:""}}</el-menu-item>
                                        <template v-else>
                                            <el-submenu :index="chapter.id + ''">
                                                <template slot="title">
                                                    <span>{{chapter.name? chapter.name:""}}</span>
                                                </template>
                                                <template v-for="unit in chapter.childs">
                                                    <el-menu-item :index="unit.id + ''" :disabled="unit.disabled"
                                                        v-if="unit.metaLeaf">{{unit.name?unit.name:""}}</el-menu-item>
                                                    <template v-else>
                                                        <el-submenu :index="unit.id + ''">
                                                            <template slot="title">
                                                                <span>{{unit.name? unit.name:""}}</span>
                                                            </template>
                                                            <template v-for="lesson in unit.childs">
                                                                <el-menu-item :index="lesson.id + ''"
                                                                    :disabled="lesson.disabled" v-if="lesson.metaLeaf">
                                                                    {{lesson.name?lesson.name:""}}
                                                                </el-menu-item>
                                                                <template v-else>
                                                                    <el-submenu :index="lesson.id + ''">
                                                                        <template slot="title">
                                                                            <span>{{lesson.name? lesson.name:""}}</span>
                                                                        </template>
                                                                        <template v-for="cls in lesson.childs">
                                                                            <el-menu-item :index="cls.id + ''"
                                                                                :disabled="cls.disabled"
                                                                                v-if="cls.metaLeaf">
                                                                                {{cls.name?cls.name:""}}
                                                                            </el-menu-item>
                                                                        </template>
                                                                    </el-submenu>
                                                                </template>
                                                            </template>
                                                        </el-submenu>
                                                    </template>
                                                </template>

                                            </el-submenu>
                                        </template>
                                    </template>

                                </el-menu>
                            </vue-scroll>
                        </div>
                    </div>
                    <div class="teachList" v-loading="lessonLoading">
                        <div class="history" v-if="!lessonList && lessonGroup && lessonGroup.length&&!netError">最新备课包</div>
                        <div class="history" v-if="!lessonList && (!lessonGroup || lessonGroup.length==0)&&!netError">暂无备课包</div>
                        <div class="netError" v-if="netError" @click="pageReload">
                            <div class="error">
                                <img src="@/assets/netError.png" />
                                <p>网络似乎出了点问题，点击<a href="#">重试</a></p>
                            </div>
                        </div>
                        <vue-scroll :ops="ops">
                            <div class="lessonList">
                                <!--已选择章节下的备课包列表-->
                                <div class="lesson" v-for="(lesson,index) in lessonList" v-if="lessonList">
                                    <div class="title">
                                        <div class="name" v-for="(row,index) in lesson.title.split('\n')">{{ row }}
                                        </div>
                                        <div class="time">{{ lesson.updatedAt | formatDate}} 更新</div>
                                    </div>
                                    <div class="preview">
                                        <vue-scroll>
                                            <div class="step" v-if="lesson.sectionInfoDTO.children"
                                                v-for="(section,index) in lesson.sectionInfoDTO.children">
                                                <div class="stepName"><i></i>{{section.name? section.name:""}}</div>
                                                <div class="resourceList">
                                                    <div class="resourceItem"
                                                        v-for="(resource,index) in section.resourceInfoList"
                                                        v-if="section.resourceInfoList&&resource.resourceType!=10">
                                                        <i class="icon">
                                                            <img src="@/assets/type1.png"
                                                                v-if="resource.resourceType==1" />
                                                            <img src="@/assets/type2.png"
                                                                v-if="resource.resourceType==2" />
                                                            <img src="@/assets/type3.png"
                                                                v-if="resource.resourceType==3" />
                                                            <img src="@/assets/type4.png"
                                                                v-if="resource.resourceType==4" />
                                                            <img src="@/assets/type5.png"
                                                                v-if="resource.resourceType==5" />
                                                            <img src="@/assets/type6.png"
                                                                v-if="resource.resourceType==6" />
                                                            <img src="@/assets/type7.png"
                                                                v-if="resource.resourceType==7" />
                                                            <img src="@/assets/type8.png"
                                                                v-if="resource.resourceType==8" />
                                                            <img src="@/assets/type9.png"
                                                                v-if="resource.resourceType==9" />
                                                            <img src="@/assets/type10.png"
                                                                v-if="resource.resourceType==10" />
                                                            <img src="@/assets/type11.png"
                                                                v-if="resource.resourceType==11" />
                                                            <img src="@/assets/type12.png"
                                                                v-if="resource.resourceType==12" />
                                                            <img src="@/assets/type13.png"
                                                                v-if="resource.resourceType==13" />
                                                        </i>
                                                        {{resource.name? resource.name: ""}}
                                                    </div>
                                                </div>
                                            </div>
                                        </vue-scroll>
                                    </div>
                                    <div class="btn_start" @click="startClass(lesson)">开始授课</div>
                                </div>
                                <!--最近备课包列表-->
                                <div class="lesson" v-for="(lesson,index) in lessonGroup"
                                    v-if="!lessonList && index < 10">
                                    <div class="title">
                                        <div class="name" v-for="(row,index) in lesson.title.split('\n')">{{ row }}
                                        </div>
                                        <div class="time">{{ lesson.updatedAt | formatDate}} 更新</div>
                                    </div>
                                    <div class="preview">
                                        <vue-scroll>
                                            <div class="step" v-if="lesson.sectionInfoDTO"
                                                v-for="(section,index) in lesson.sectionInfoDTO.children">
                                                <div class="stepName"><i></i>{{section.name? section.name:""}}</div>
                                                <div class="resourceList">
                                                    <div class="resourceItem"
                                                        v-for="(resource,index) in section.resourceInfoList"
                                                        v-if="section.resourceInfoList&&resource.resourceType!=10">
                                                        <i class="icon">
                                                            <img src="@/assets/type1.png"
                                                                v-if="resource.resourceType==1" />
                                                            <img src="@/assets/type2.png"
                                                                v-if="resource.resourceType==2" />
                                                            <img src="@/assets/type3.png"
                                                                v-if="resource.resourceType==3" />
                                                            <img src="@/assets/type4.png"
                                                                v-if="resource.resourceType==4" />
                                                            <img src="@/assets/type5.png"
                                                                v-if="resource.resourceType==5" />
                                                            <img src="@/assets/type6.png"
                                                                v-if="resource.resourceType==6" />
                                                            <img src="@/assets/type7.png"
                                                                v-if="resource.resourceType==7" />
                                                            <img src="@/assets/type8.png"
                                                                v-if="resource.resourceType==8" />
                                                            <img src="@/assets/type9.png"
                                                                v-if="resource.resourceType==9" />
                                                            <img src="@/assets/type10.png"
                                                                v-if="resource.resourceType==10" />
                                                            <img src="@/assets/type11.png"
                                                                v-if="resource.resourceType==11" />
                                                            <img src="@/assets/type12.png"
                                                                v-if="resource.resourceType==12" />
                                                            <img src="@/assets/type13.png"
                                                                v-if="resource.resourceType==13" />
                                                        </i>
                                                        {{resource.name? resource.name: ""}}
                                                    </div>
                                                </div>
                                            </div>
                                        </vue-scroll>
                                    </div>
                                    <div class="btn_start" @click="startClass(lesson)">开始授课</div>
                                </div>
                            </div>
                        </vue-scroll>

                    </div>
                </div>
            </div>
            <!--已选择的备课包资源-->
            <div class="lessonItem" v-show="currLesson">
                <div class="lessonTitle" v-if="currLesson">{{currLesson.title}}</div>
                <div class="resources" v-if="currLesson">
                    <vue-scroll>
                        <div class="step" v-for="(section,index) in currLesson.sectionInfoDTO.children">
                            <div class="stepName"><i></i>{{section.name?section.name:""}}</div>
                            <div class="resourceList">
                                <div class="resourceItem" v-for="(resource,index) in section.resourceInfoList"
                                    v-if="section.resourceInfoList&&resource.resourceType!=10"
                                    @click="loadResource(resource.resourceId,resource.resourceType,resource.content,resource.name,resource.paperNode?resource.paperNode.typesetId:'',resource.paperNode?resource.paperNode.paperShortCode:'',resource.paperNode?resource.paperNode.userId:'')">
                                    <i class="icon">
                                        <img src="@/assets/type1.png" v-if="resource.resourceType==1" />
                                        <img src="@/assets/type2.png" v-if="resource.resourceType==2" />
                                        <img src="@/assets/type3.png" v-if="resource.resourceType==3" />
                                        <img src="@/assets/type4.png" v-if="resource.resourceType==4" />
                                        <img src="@/assets/type5.png" v-if="resource.resourceType==5" />
                                        <img src="@/assets/type6.png" v-if="resource.resourceType==6" />
                                        <img src="@/assets/type7.png" v-if="resource.resourceType==7" />
                                        <img src="@/assets/type8.png" v-if="resource.resourceType==8" />
                                        <img src="@/assets/type9.png" v-if="resource.resourceType==9" />
                                        <img src="@/assets/type10.png" v-if="resource.resourceType==10" />
                                        <img src="@/assets/type11.png" v-if="resource.resourceType==11" />
                                        <img src="@/assets/type12.png" v-if="resource.resourceType==12" />
                                        <img src="@/assets/type13.png" v-if="resource.resourceType==13" />
                                    </i>
                                    {{resource.name?resource.name:""}}
                                </div>
                            </div>
                        </div>
                    </vue-scroll>
                </div>
                <div class="btn_start" @click="switchClass">选择其他课程</div>
            </div>
        </div>
    </div>
</template>
<script>
    import { formatDate } from '@/assets/date.js';
    import utils from '@/assets/utils.js';
    import token from '@/assets/token.js';
    import API from '@/assets/api.js'
    import axios from 'axios'
    import qs from 'qs'
    export default {
        name: 'App',
        data() {
            return {
                bookId: null,
                bookName: "",
                schoolId: null,
                currOpenIndex: "",
                gradeId: null,
                subjectId: null,
                versionId: null,
                netError: false,
                lessonLoading: false,
                baseLoading: true,
                ops: {
                    scrollPanel: {
                        scrollingX: true,
                        scrollingY: false
                    }
                },
                dpr: 1,
                timer: null,
                bookList: [],
                chapterList: [],
                lessonGroup: null,
                lessonList: [],
                activeIndex: "",
                currLesson: null
            }
        },
        filters: {
            formatDate(time) {
                let date = new Date(time);
                return formatDate(date, 'yyyy-MM-dd hh:mm');
            },
            isDidsabled(id) {
                console.log(this.lessonGroup)
                for (const lesson of this.lessonGroup) {
                    if (lesson.id == id) {
                        return false
                    }
                }
                return true
            }
        },
        mounted() {


        },
        beforeMount() {
            //获取url参数
            this.bookId = parseInt(utils.getUrlPrams("bookId"))
            console.log(this.bookId)
            this.bookName = utils.getUrlPrams("bookName")
            this.schoolId = utils.getUrlPrams("schoolId")
            this.userId = utils.getUrlPrams("userId")
            this.gradeId = utils.getUrlPrams("gradeId")
            this.subjectId = utils.getUrlPrams("subjectId")
            this.versionId = utils.getUrlPrams("versionId")
            this.dpr = localStorage.getItem("width") / 1920
            this.loadChapterInfo(this.bookId)
            this.loadBookList()
        },
        methods: {
            iconUrl(type) {
                return "@/assets/type" + type + ".png"
            },
            pageReload() {
                // console.log("222")
                location.reload()
            },
            changeBook(bookId) {
                this.bookId = bookId
                this.baseLoading = true
                this.activeIndex = ""
                this.closeCurrMenu()
                this.loadChapterInfo(bookId)
            },
            switchClass() {
                this.currLesson = null
                this.changeMode()
            },
            startClass(lesson) {
                console.log(lesson)
                this.currLesson = lesson
                this.changeMode()
            },
            selectLesson(index, indexPath) {
                console.log(index, indexPath);
                this.activeIndex = index
                let lessonList = []
                for (const lesson of this.lessonGroup) {
                    if (lesson.chapterId == index) {
                        lessonList.push(lesson)
                    }
                }
                console.log(this.lessonList)
                this.lessonList = lessonList
            },
            openSubmenu(index, indexPath) {
                console.log(index, indexPath);
                this.currOpenIndex = indexPath[0]
                if (index == this.chapterList[this.chapterList.length - 1].id) {
                    this.$refs['vsMenu'].scrollBy(
                        {
                            dy: "500"
                        },
                        500,
                        'easeOutCubic'
                    );
                }
            },
            closeCurrMenu() {
                this.$refs.tMenu.close(this.currOpenIndex)
            },
            loadResource(id, type, url, name, paper, short, uid) {
                console.log(id, type, encodeURIComponent(url), name, paper, short, uid)
                window.cefQuery({
                    request: "loadResource:" + id + "&" + + type + "&" + encodeURIComponent(url) + "&" + name + "&" + paper + "&" + short + "&" + uid,   // 代表请求方法 example事件名称 data 为传入参数
                    onsuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            changeMode() {
                let size = 0
                if (this.currLesson) {
                    size = 1
                }
                /*
                @size: 0列表  1详情
                */
                window.cefQuery({
                    request: "changeTMSize:" + size,   // 代表请求方法 example事件名称 data 为传入参数
                    onsuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            closeWindow() {
                window.cefQuery({
                    request: "closeWindowTeachMaterial",   // 代表请求方法 example事件名称 data 为传入参数
                    onsuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            loadBookList() {
                var that = this
                let gradeId = this.gradeId
                let subjectId = this.subjectId
                let versionId = this.versionId
                axios.get(API.api_qb + "tal/getVolumeList", {
                    params: {
                        gradeId: gradeId,
                        subjectId: subjectId,
                        versionId: versionId
                    }
                }).then(function (res) {
                    if (res.data.data) {
                        that.bookList = res.data.data
                    }
                }).catch(function (error) {
                    console.log(error);

                });
            },
            loadChapterInfo(id) {
                this.lessonList = null

                var that = this
                let bookId = id
                axios.get(API.api_qb + "tal/getChapter", {
                    params: {
                        bookId: bookId
                    }
                }).then(function (res) {
                    clearInterval(that.timer)
                    that.baseLoading = false
                    let chapter = res.data.data
                    that.chapterList = chapter
                    that.loadMeterialList(bookId)
                }).catch(function (error) {
                    console.log(error);
                    that.lessonLoading = false
                    that.baseLoading = false
                    that.netError = true
                });
            },
            loadMeterialList(id) {
                this.lessonLoading = true
                var that = this
                let bookId = id
                let userId = this.userId
                let schoolId = this.schoolId
                axios.get(API.api_qb + "tal/getTeachMateria", {
                    // axios.get("http://192.168.10.101:8081/tal/getTeachMateria", {
                    params: {
                        bookId: bookId,
                        thirdUid: userId,
                        schoolId: schoolId
                    }
                }).then(function (res) {
                    that.lessonLoading = false
                    let list;
                    console.log(res.data.data)
                    if (res.data.data) {
                        list = res.data.data.teachingProcedureList
                        that.lessonGroup = list
                        console.log("---------------")
                        console.log(that.lessonGroup)
                        let isDidsabled;
                        for (let j = 0; j < that.chapterList.length; j++) {
                            const chapter = that.chapterList[j];
                            if (chapter.childs) {
                                for (let k = 0; k < chapter.childs.length; k++) {
                                    const jie = chapter.childs[k];
                                    isDidsabled = true
                                    if (jie.metaLeaf) {
                                        for (let i = 0; i < list.length; i++) {
                                            isDidsabled = true
                                            const lesson = list[i];
                                            if (lesson.chapterId == chapter.childs[k].id) {
                                                isDidsabled = false
                                                break
                                            }
                                        }
                                        that.$set(that.chapterList[j].childs[k], "disabled", isDidsabled)
                                    } else {
                                        for (let l = 0; l < jie.childs.length; l++) {
                                            const unit = jie.childs[l];
                                            if (unit.metaLeaf) {
                                                for (let i = 0; i < list.length; i++) {
                                                    isDidsabled = true
                                                    const lesson = list[i];
                                                    if (lesson.chapterId == unit.id) {
                                                        isDidsabled = false
                                                        break
                                                    }
                                                }
                                                that.$set(that.chapterList[j].childs[k].childs[l], "disabled", isDidsabled)
                                            } else {
                                                for (let m = 0; m < unit.childs.length; m++) {  
                                                    const cls = unit.childs[m];
                                                    // console.log(cls.name)
                                                    if (cls.metaLeaf) {
                                                        for (let i = 0; i < list.length; i++) {
                                                            isDidsabled = true
                                                            const lesson = list[i];
                                                            if (lesson.chapterId == cls.id) {
                                                                isDidsabled = false
                                                                break
                                                            }
                                                        }
                                                        that.$set(that.chapterList[j].childs[k].childs[l].childs[m], "disabled", isDidsabled)
                                                    }
                                                }
                                            }
                                        }
                                    }

                                }
                            }

                        }
                        console.log(that.chapterList)
                    } else {
                        for (let j = 0; j < that.chapterList.length; j++) {
                            const chapter = that.chapterList[j];
                            console.log(chapter)
                            if (!chapter.metaLeaf) {
                                for (let k = 0; k < chapter.childs.length; k++) {
                                    const jie = chapter.childs[k];
                                    if (jie.metaLeaf) {
                                        that.$set(that.chapterList[j].childs[k], "disabled", true)
                                    } else {
                                        for (let l = 0; l < jie.childs.length; l++) {
                                            const unit = jie.childs[l];
                                            if (unit.metaLeaf) {
                                                that.$set(that.chapterList[j].childs[k].childs[l], "disabled", true)
                                            } else {
                                                for (let m = 0; m < unit.childs.length; l++) {
                                                    const cls = unit.childs[l];
                                                    that.$set(that.chapterList[j].childs[k].childs[l].childs[m], "disabled", true)
                                                }
                                            }
                                        }
                                    }

                                }
                            }

                        }
                        console.log(that.chapterList)
                        that.lessonGroup = null
                    }


                }).catch(function (error) {
                    console.log(error);
                    that.lessonLoading = false
                    that.baseLoading = false
                    that.netError = true
                });
            }
        }
    }
</script>
<style>
    * {
        margin: 0;
        padding: 0;
    }


    #app {
        font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #333;
        width: 100%;
        height: 100%;
    }

    .el-loading-spinner .path {
        stroke: #8BADCE !important;
    }

    .el-input__inner {
        color: #2E4A66 !important;
        font-size: 0.12rem;
        border: 0.01rem solid #D5E2ED !important;
    }

    .el-cascader-menu__wrap {
        height: 2rem;
    }

    .el-cascader-node {
        padding: 0 0.3rem 0 0.2rem;
        height: 0.34rem;
        line-height: 0.34rem;
    }

    .el-select {
        width: 100%;
    }

    .el-select .el-input .el-select__caret {
        font-size: 0.14rem;
    }

    .el-input--small .el-input__icon {
        line-height: 0.3rem;
    }

    .el-input__icon {
        width: 0.25rem;
    }

    .el-select-dropdown__item {
        color: #2E4A66 !important;
        font-size: 0.12rem;
        padding: 0 0.1rem;
        height: 0.3rem;
        line-height: 0.3rem;
    }

    .el-popper[x-placement^=bottom] .popper__arrow {
        display: none;
    }

    .el-menu {
        border-right: none;
    }

    .el-menu-item,
    .el-submenu__title {
        height: 0.4rem;
        line-height: 0.4rem;
    }

    .el-submenu__icon-arrow {
        right: 0.2rem !important;
    }

    .el-cascader-menu {
        font-size: 0.14rem !important;
        color: #2E4A66 !important;
        min-width: 1.8rem;
    }

    .el-submenu__title {
        box-sizing: border-box;
        width: 100%;
        text-align: left;
        font-size: 0.14rem !important;
        font-weight: bold;
        padding-right: 0.4rem !important;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }

    .el-submenu__title i {
        color: #6C8093;
    }

    .el-submenu .el-menu-item {
        height: 0.3rem;
        line-height: 0.3rem;
        padding: 0 0.2rem 0 0.4rem !important;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }

    .el-submenu .el-submenu .el-menu-item {
        padding: 0 0.2rem 0 0.6rem !important;
    }

    .el-submenu .el-submenu .el-submenu .el-menu-item {
        padding: 0 0.2rem 0 0.8rem !important;
    }

